body {
  margin: 0;
  font-family: 'Karla';
  color: #293264;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.questionParagraph{
  font-weight: 700;
}


.correctLabelChoice,
.incorrectLabelChoice,
.selectedLabelChoice,
.greyLabelChoice,
.labelChoice {
  border: 1px solid #4D5B9E;
  padding: 5px 15px;
  border-radius: 8px;
  display:inline-block;
}

.correctLabelChoice {
  background-color: #94D7A2;
  border: 1px solid #94D7A2;
}

.incorrectLabelChoice {
  background-color: #F8BCBC;
  border: 1px solid #F8BCBC;
  color: #8F94AF;
}

.selectedLabelChoice {
  border: 1px solid #D6DBF5;
  background-color:#D6DBF5;
}

.greyLabelChoice{
  border: 1px solid #A1A1CC;
  color: #8F94AF;
}

.radioChoice {
  visibility: hidden;
  height: 0;
  width: 0;
}

.containerForChoices {
  display: flex;
}

.startQuizButton,
.revealAnswersButton {
  background-color: #4D5B9E;
  border: none;
  color: #F5F7FB;
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  padding: 10px 25px;
  border-radius: 10px;
}

.flexContainer{
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.scoreAndButtonContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.quizDataContainer {
  margin-left: auto;
  margin-right: auto;
  width: 700px;
}

.questionItem {
  border: 1px solid blue;
}

.score {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  font-weight: 700;
}

hr {
  margin-top: 20px;
  border: 1px solid #DBDEF0;
}
.flexContainer > *{
  margin: 0;
}